import {
  Component,
  Inject,
  Input,
  OnInit,
} from '@angular/core';
import {
  FormControl,
  FormGroup,
} from '@angular/forms';

import { DialogService } from 'ng2-bootstrap-modal';

import { OpenModalService } from '../../../../core/services/open-modal.service';
import { AuthService } from '../../../../core/services/auth.service';
import { RegistrationComponent } from '../../../registration/pages/registration/registration.component';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { PrefillAddressService } from '../../../../core/services/prefill-address.service';
import { StateManagerService } from '../../../../core/services/state-manager.service';

interface Floor {
  id: number;
  text: string;
}

const FLOORS_COUNT: number = 40;

@Component({
  selector: 'app-property-detail-add-apartment',
  templateUrl: './property-detail-add-apartment.component.html',
  styleUrls: ['./property-detail-add-apartment.component.css']
})
export class AddApartmentComponent implements OnInit  {
  @Input() marker: ExtendedMarkerModel;

  public _name: string = 'property-detail-add-apartment';
  public claimingForm: FormGroup;

  public get location(): FormControl {
    return this.claimingForm.get('location') as FormControl;
  }

  public get floor(): FormControl {
    return this.claimingForm.get('floor') as FormControl;
  }

  private initClaimingForm(): void {
    this.claimingForm = new FormGroup({
      'floor': new FormControl(''),
      'location': new FormControl(''),
    });
  }

  public get floors(): Array<Floor> {
    return Array.from({length: FLOORS_COUNT}, (value, key) => {
      return {id: key + 1, text: (key + 1).toString()};
    });
  }

  constructor(
    private dialogService: DialogService,
    private openModalService: OpenModalService,
    private authService: AuthService,
    private prefillAddressService: PrefillAddressService,
    private stateManagerService: StateManagerService,
  ) {}

  public ngOnInit(): void {
    this.initClaimingForm();
  }

  public addApartment(): void {
    const role = 'global';
    const activeTab = this.authService.isUserLoggedIn() ? 'address' : 'registration';
    const mode = activeTab === 'address' ? 'add_property' : 'registration';
    let nextStep = '';

    if (!this.authService.isUserLoggedIn() && activeTab === 'registration') {
      this.stateManagerService.setPropertyIdForNewHolder(this.marker.id);

      nextStep = 'add_property';
    }

    this.prefillAddressService.setAddressFields({
      floor: this.floor.value,
      location: this.location.value
    });
    this.dialogService.addDialog(
      RegistrationComponent,
      { role, activeTab, mode, nextStep, needPrefillData: true }
    )
    .subscribe(() => {
      this.openModalService.close();
    });
  }
}
