import {
  Component,
  OnInit,
  Input, Output,
  EventEmitter
} from '@angular/core';

import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-marker-detail-sales-info',
  templateUrl: './marker-detail-sales-info.component.html',
  styleUrls: ['./marker-detail-sales-info.component.css']
})

export class MarkerDetailSalesInfoComponent implements OnInit  {
  @Input() model: any = null;

  @Output() openContactMeForm: EventEmitter<Object> = new EventEmitter<Object>();

  public rawTemporaryWishedPrice: any = 0;
  public currentTemporaryWishedPrice: any = 0;

  public priceSlider: FormGroup;
  public lastModelId: string = null;
  public _name: string = 'marker-detail-sales-info';
  public tooltipContent: string = ` Bei den hier angegebenen Werten handelt es sich lediglich um Schätzungen und
          nicht um verbindliche Angaben.Dieser Wert wird anhand der Lage des Objekts
          ermittelt und spiegelt einen allgemeinen Wert ohne Berücksichtigung der konkreten
          Ausstattung, Zustand etc. wider.`;

  public get maxPriceEstimation() {
    return Math.round((Math.round((this.model.EstimatedPrice * 1.25) / 1000)) / 10) * 10;
  }

  public get minPriceEstimation() {
    return Math.round((Math.round((this.model.EstimatedPrice * 0.75) / 1000)) / 10) * 10;
  }

  public get minIntermediatePriceEstimation() {
    return Math.round((Math.round((this.model.EstimatedPrice  * 0.95) / 1000)) / 10) * 10;
  }

  public get maxIntermediatePriceEstimation() {
    return Math.round((Math.round((this.model.EstimatedPrice  * 1.05) / 1000)) / 10) * 10;
  }

  constructor(private formBuilder: FormBuilder) {
    this.priceSlider = this.formBuilder.group({'single': 0});
  }

  public ngOnInit() {
    if (this.model) {
      this.lastModelId = this.model.id;
      this.rawTemporaryWishedPrice = this.model.WishedPrice;
      this.currentTemporaryWishedPrice = this.model.Listing ?
        this.model.SalePrice.toLocaleString('de-DE') : this.model.WishedPrice.toLocaleString('de-DE');
    }
  }

  public _contactMe() {
    this.openContactMeForm.emit({title: 'Besichtigungstermin vereinbaren'});
  }
}
