import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { DialogService, DialogComponent } from 'ng2-bootstrap-modal';

import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { MapFilterService } from '../../../../core/services/map-filter.service';

export interface OwnerContactModel {
  title:        string;
  buttonText:   string;
  feedbackText: string;
}

@Component({
  selector: 'app-owner-contact',
  templateUrl: './owner-contact.component.html',
  styleUrls: ['./owner-contact.component.css']
})

export class OwnerContactComponent
  extends DialogComponent <OwnerContactModel, string>
  implements OwnerContactModel, OnInit, OnDestroy {

  public title: string;
  public buttonText: string;
  public feedbackText: string;
  public messageText: string;

  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isMobileDevice: boolean = document.body.clientWidth < 761;

  public isMessageSend: boolean = false;

  public _name: string = 'owner-contact';

  public placeholder: string;

  private _listener(): void {
    this.isMobileDevice = document.body.clientWidth < 760;
  }

  constructor(
    public dialogService: DialogService,
    private mapFilterService: MapFilterService,
    private router: Router,
  ) {
    super(dialogService);

    this._listener = this._listener.bind(this);
  }

  public ngOnInit(): void {
    window.addEventListener('resize', this._listener);
  }

  public ngOnDestroy(): void {
    super.ngOnDestroy();
    window.removeEventListener('resize', this._listener);
  }

  public get imageUrl(): string {
    const name: string = this.isMessageSend ? 'paper-plane.svg' : 'hand-home.svg';
    return `../../../../assets/images/${name}`;
  }

  public onLogoClick(): void {
    this.router.navigate(['/map']);
    if (this.isSmallMobileDevice) {
      setTimeout(() => {
        this.mapFilterService.toggleFiltersShown(false);
      }, 500);
    }
    this.close();
  }

  public apply(): void {
    if (this.messageText == null || this.messageText === '') return;

    // @TODO: send message
    this.isMessageSend = true;
  }

  // @FIXME: later check functionality for every button at the last step
  public _close(): void {
    this.router.navigate(['/map']);
    if (this.isSmallMobileDevice) {
      setTimeout(() => {
        this.mapFilterService.toggleFiltersShown(false);
      }, 500);
    }
    this.close();
  }
}
