import {
  Component,
  OnInit,
  OnDestroy,
  ChangeDetectorRef,
  AfterViewChecked,
  Inject,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { NguCarouselConfig } from '@ngu/carousel';
import { DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';
import { Subscription } from 'rxjs';

import {
  MarkerDetailContactMeWindowComponent
} from '../../../../shared/components/marker-detail-contact-me-window/marker-detail-contact-me-window.component';
import {
  LoginComponent,
  LoginResultType,
} from '../../../registration/components/login/login.component';
import {
  MarkerDetailGalleryViewWindowComponent
} from '../../../../shared/components/marker-detail-gallery/marker-detail-gallery-view-window/marker-detail-gallery-view-window.component';
import { RegistrationComponent } from '../../../registration/pages/registration/registration.component';
import {
  MarkerDetailMakeMeMoveComponent
} from '../../../../shared/components/marker-detail-make-me-move/marker-detail-make-me-move.component';
import { AuthService } from '../../../../core/services/auth.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { StateManagerService } from '../../../../core/services/state-manager.service';
import { OpenModalService } from '../../../../core/services/open-modal.service';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { splitMarkersIntoGroups } from '../../../../shared/utils/markers-group-splitter';
import { WINDOW } from '../../../../core/services/window.provider';
import { OfferData } from '../../../../core/models/properties-repository.model';
import { Offer } from '../../../../core/models/offers.model';
import { LoggerService } from '../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../core/services/logger/logger.provider';
import { UserService } from '../../../../core/services/user.service';
import { isNullOrUndefined } from '../../../../shared/utils/null-or-undefined.util';
import { EmbededAppartment } from '../../../../core/models/embeded-appartment.model';

interface Aggregation {
  id: string;
  price: number;
  EstimatedPrice: number;
  EstimatedPricePerM2: number;
  area: number;
}

interface OwnerSettings {
  role: string;
  activeTab: string;
  mode: string;
  needPrefillData: boolean;
}

type PropertySectionType = 'overview' | 'marketView';

@Component({
  selector: 'app-property-detail-page',
  templateUrl: './property-detail.component.html',
  styleUrls: ['./property-detail.component.css']
})
export class PropertyDetailComponent implements OnInit, AfterViewChecked, OnDestroy {

  private authSubscription: Subscription = null;

  public _name: string = 'property-detail';
  public activeSection: PropertySectionType = 'overview';
  public step: string = '';
  public offerPrice: number = null;
  // 56 - This is height of header component.
  public displayHeight: string = `${this.window.document.body.clientHeight - 56}px`;

  public isLoad: boolean = false;
  public userNotAuthorized: boolean = true;
  public isSmallMobileDevice: boolean = isSmallMobileDevice();

  public markers: Array<Array<ExtendedMarkerModel>> = [];
  public offerPropertyCombination: Aggregation = null;

  public propertyFillLevelWidth: string = null;
  public propertyFillLevelValue: number = 0;

  public chosenOffer: Offer;
  public carouselTile: NguCarouselConfig;
  public activeMarker: ExtendedMarkerModel = null;
  public sliderTitle: string = 'Neueste Immobilien die zum Verkauf stehen';

  public get shouldStickyNavBeDisplayed(): boolean {
    return !this.userNotAuthorized && this.activeMarker.isOwnedBy(this.userService.uuid);
  }

  public get isMarkerViewActive(): boolean {
    return this.activeSection === 'marketView';
  }

  public get isOverviewActive(): boolean {
    return this.activeSection === 'overview';
  }

  public get isMarkersListNotEmpty(): boolean {
    return this.markers.length > 0;
  }

  private async loadMarkersList(): Promise<void> {
    try {
      const extendedMarkersList = await this.propertiesRepositoryService.getDataByUrl('property', this.activeMarker.id);
      this.markers = splitMarkersIntoGroups(extendedMarkersList);
      this.isLoad = (this.markers != null && this.markers.length > 0);
    } catch (error) {
      this.isLoad = true;
      this.logger.error('PropertyDetailComponent::loadMarkersData', error);
    }
  }

  private async updateMarker(): Promise<void> {
    await this.propertiesRepositoryService.reloadProperty(this.activeMarker);
  }

  private async openSignUpWindow(after?: string): Promise<void> {
    const modalRef = this.modalService.openFromComponent(LoginComponent);
    modalRef.result.then(result => {
      if (result && result === LoginResultType.SuccessLogin) {
        if (after === 'makeMeOwner') {
          this.makeMeOwner();
        } else if (after === 'loadImages') {
          this.loadImages();
        }
      }
    }, () => {});
  }

  private async openMakeMeOwnerWindow(): Promise<void> {
    let property: ExtendedMarkerModel | EmbededAppartment;
    if (!isNullOrUndefined(this.stateManagerService.assignedEmbededAppartment)) {
      property = this.stateManagerService.assignedEmbededAppartment;
    } else {
      property = this.activeMarker;
    }
    let settings: OwnerSettings = {role: 'global', activeTab: '', mode: '', needPrefillData: false};
    switch (property.type) {
      case 'MFH':
        settings = {role: 'global', activeTab: 'address', mode: 'add_property', needPrefillData: true};
        break;
      case 'EFH':
        settings = {role: 'global', activeTab: 'verify', mode: 'set_owner', needPrefillData: false};
        break;
      case 'ETW':
        settings = {role: 'global', activeTab: 'verify', mode: 'set_owner', needPrefillData: false};
        break;
    }
    this.openModalService.open();
    this.dialogService
      .addDialog(
        RegistrationComponent,
        { ...settings }
      )
      .subscribe(_ => {
        this.stateManagerService.assignedEmbededAppartment = null;
        this.openModalService.close();
      });
  }

  private calcFillLevel(marker: ExtendedMarkerModel): void {
    const fields: string[] = [
      'Floor', 'Door', 'PropertySubType', 'HistoricalProperty', 'LeaseHold',
      'Rented', 'ConstructionYear', 'LastModernisationYear', 'RoomsNumber',
      'FloorNumber', 'LivingSurface', 'UsableArea', 'TotalArea', 'HeatingType',
      'EnergyCertificate', 'EnergyGrade', 'RoofCovering', 'Windows', 'Floor',
      'PropertyCondition', 'PropertySubType', 'FloorLocation', 'Title',
      'EnergyCertificateStatus', 'EnergyConsumptionClass', 'FacilitiesQuality',
      'EnergySource', 'HeatingType',
    ];

    const subFields: string[] = [
      'Cellar', 'Elevator', 'Garage', 'Parking', 'Garden', 'Kitchen', 'Terrace',
      'Balcony', 'Chimney', 'AirConditioned', 'Sauna', 'SwimmingPool'
    ];

    fields.forEach((item) => {
      if (marker[item] != null && marker[item] !== '' && marker[item] !== 0) {
        this.propertyFillLevelValue += 5;
      }
    });

    if (subFields.some((item) => marker[item] != null && marker[item] !== '')) {
      this.propertyFillLevelValue += 5;
    }

    if (this.propertyFillLevelValue > 100) this.propertyFillLevelValue = 100;

    const extraLevelWidth: number = Math.floor(this.propertyFillLevelValue / 20) * 3;

    this.propertyFillLevelWidth = `calc(${this.propertyFillLevelValue}% + ${extraLevelWidth}px)`;
  }

  private async loadOffersData(): Promise<void> {
    if (this.userNotAuthorized) {
      this.offerPrice = null;
      this.offerPropertyCombination = null;
    } else {
      try {
        const response: OfferData = await this.propertiesRepositoryService.getOffersDataForUser().toPromise();
        if (response && response.results) {
          this.chosenOffer = response.results.find(item => item.property && item.property.uuid === this.activeMarker.id);
          if (this.chosenOffer) {
            this.offerPropertyCombination = this.combineOfferWithProperty(this.activeMarker, this.chosenOffer);
            this.offerPrice = this.chosenOffer.price;
          }
        }
      } catch (error) {
        this.logger.error('PropertyDetailPage::loadOffersData:Error', error.message);
      }
    }
  }

  private goToDashboardPage(): void {
    this.stateManagerService.markerIdForLoadingImage = this.activeMarker.id;
    this.router.navigate(['/profile/my-property']);
  }

  private async getPropertyInfo(): Promise<void> {
    const id: string = this.route.snapshot.paramMap.get('id');

    if (id) this.activeMarker = await this.propertiesRepositoryService.getMarkerDetails(id);
    // @FIXME: delete it after will make full support for params
    else this.activeMarker = this.stateManagerService.activeMarker;

    this.propertiesRepositoryService.setPropertyVisited(this.activeMarker.id);

    this.stateManagerService.activeMarker = this.activeMarker;
    if (this.activeMarker != null) {
      this.calcFillLevel(this.activeMarker);
      await this.loadMarkersList();
    }
    this.userNotAuthorized = !this.authService.isUserLoggedIn();

    if (!this.userNotAuthorized && this.activeMarker != null) {
      await this.loadOffersData();
    }

    this.authSubscription = this.authService.events.subscribe(() => {
      this.userNotAuthorized = !this.authService.isUserLoggedIn();
      if (this.activeMarker != null && this.activeMarker.id != null && this.activeMarker.id !== `${void 0}`) {
        this.updateMarker();
        this.loadOffersData();
      }
    });
  }

  private combineOfferWithProperty(marker: ExtendedMarkerModel, offer: Offer): Aggregation {
    const aggregation = {} as Aggregation;

    aggregation.id = offer.uuid;
    aggregation.price = offer.price;
    aggregation.EstimatedPrice = marker.EstimatedPrice;
    aggregation.EstimatedPricePerM2 = 1;       // ?? do we need it for offers
    aggregation.area = 1;                      // ?? do we need it for offers

    return aggregation;
  }

  constructor(
    private modalService: NgxModalStack,
    private authService: AuthService,
    private userService: UserService,
    private propertiesRepositoryService: PropertiesRepositoryService,
    private stateManagerService: StateManagerService,
    private cdRef: ChangeDetectorRef,
    private openModalService: OpenModalService,
    private dialogService: DialogService,
    private route: ActivatedRoute,
    private router: Router,
    @Inject(LOGGER) private logger: LoggerService,
    @Inject(WINDOW) private window: Window,
  ) {}

  public async ngOnInit(): Promise<void> {
    await this.getPropertyInfo();
    this.carouselTile = {
      grid: {xs: 2, sm: 3, md: 3, lg: 5, all: 0},
      slide: 1,
      speed: 400,
      animation: 'lazy',
      point: {
        visible: false,
      },
      touch: false,
      easing: 'ease',
    };
  }

  public ngAfterViewChecked(): void {
    this.cdRef.detectChanges();
  }

  public ngOnDestroy(): void {
    if (this.authSubscription) this.authSubscription.unsubscribe();
  }

  public transformCertificateStatus(arr, text) {
    if (!text) return '-';
    else return arr.find(item => item.textBe === text).shortText;
  }

  public async makeMeOwner(): Promise<void> {
    try {
      let makeOwnerState: number = 2;
      let userInfo: any = {};

      do {
        if (!this.authService.isUserLoggedIn()) {
          makeOwnerState = 0;
          break;
        }

        userInfo = await this.authService.getUserAttributes() as any;
        if (userInfo.phone_number_verified == null || userInfo.phone_number_verified === 'false') {
          makeOwnerState = 1;
          break;
        }

        break;
      } while (false);

      if ([0, 1].includes(makeOwnerState)) {
        this.stateManagerService.setPropertyIdForNewHolder(this.activeMarker.id);
        this.stateManagerService.needMakeMeOwner = true;
      }

      if (makeOwnerState === 0) {
        return this.openSignUpWindow('makeMeOwner');
      }
      if (makeOwnerState === 1) {
        return this.openMakeMeOwnerWindow();
      }
      if (makeOwnerState === 2) {
        return this.openMakeMeOwnerWindow();
      }

    } catch (error) {
      this.logger.error('Error in makeMeOwner:', error);
    }
  }

  public async openSlideshow(): Promise<void> {
    const activeImageIndex: number = 0;
    const images: string[] = this.activeMarker.images;
    this.openModalService.open();
    await this.dialogService.addDialog(MarkerDetailGalleryViewWindowComponent, {images, activeImageIndex}).toPromise();
    this.openModalService.close();
  }

  public toggleMakeMeMoveModal(action: string): void {
    this.openModalService.open();
    this.dialogService.addDialog(
      MarkerDetailMakeMeMoveComponent,
      {
        model: this.offerPropertyCombination || this.activeMarker,
        chosenOffer: this.chosenOffer,
        actionWithOfferPrice: action,
        page: this._name
      }
    ) .subscribe((message) => {
      if (message && message === 'offer price is set successfully') {
        this.loadOffersData();
      }
      this.openModalService.close();
    });
  }

  public async openContactMeForm({
    type,
    title = 'Stelle dem Eigentümer eine Frage!',
    buttonText = 'Frage abschicken'
  }): Promise<void> {
    this.openModalService.open();
    // @TODO: await was deleted because promise doesn't resolved for unknown reasons
    this.dialogService.addDialog(
      MarkerDetailContactMeWindowComponent,
      {type, title, buttonText, propertyId: this.activeMarker.id}
    );
    this.openModalService.close();
  }

  public activateSection(section: PropertySectionType): void {
    this.activeSection = section;
  }

  public async loadImages(): Promise<void> {
    if (!this.authService.isUserLoggedIn()) {
      this.openSignUpWindow('loadImages');
    } else if (!this.activeMarker.isOwnedBy(this.userService.uuid)) {
      await this.makeMeOwner();
    } else {
      this.goToDashboardPage();
    }
  }
}
