import {
  Component,
  Input, Output,
  EventEmitter,
} from '@angular/core';
import { Router } from '@angular/router';

import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { UserService } from '../../../../core/services/user.service';

@Component({
  selector: 'app-overview-property',
  templateUrl: './overview-property.component.html',
  styleUrls: ['./overview-property.component.css']
})
export class OverviewPropertyComponent {
  @Input() marker: ExtendedMarkerModel;

  @Output() makeMeOwner: EventEmitter<string> = new EventEmitter<string>();

  public _name: string = 'overview-property';
  public userMode: string = 'show';
  public isPropertyDataShown: boolean = true;
  public isPropertyEquipmentShown: boolean = true;
  public isPropertyDescriptionShown: boolean = true;
  public isPropertyEnergyShown: boolean = true;

  public get isPropertyHasEquipment(): boolean {
    return this.checkComfort(this.marker);
  }

  public get isPropertyHasDescription(): boolean {
    return !!this.marker.Description;
  }

  public get isPropertyHasEnergyInfo(): boolean {
    return this.checkEnergyInfo(this.marker);
  }

  public get userRole(): string {
    return this.userService.role;
  }

  private checkComfort(marker: ExtendedMarkerModel): boolean {
    const comfort = marker.Kitchen || marker.Elevator || marker.Garage || marker.Parking ||
    marker.Terrace || marker.Balcony || marker.Chimney || marker.AirConditioned ||
    marker.Sauna || marker.SwimmingPool || marker.HasDryWashSpace || marker.BikeRoom ||
    marker.VisitorsWC || marker.Garden || marker.Cellar || marker.HistoricalProperty ||
    marker.SecondaryEntrance || marker.Furnished || marker.BarrierFree;

    return !!comfort;
  }

  private checkEnergyInfo(marker: ExtendedMarkerModel): boolean {
    const energy = marker.EnergyCertificate || marker.EnergyCertificateFileId || marker.EnergyCertificateStatus ||
      marker.EnergyCertificateType || marker.EnergyConsumptionClass || marker.EnergyConsumptionValue ||
      marker.EnergySource || marker.HeatingType;
    return !!energy ;
  }

  constructor(
    private userService: UserService,
    private router: Router,
  ) {}

  public emitMakeMeOwner(): void {
    this.makeMeOwner.emit('own property from overview');
  }

  public toggleDataSection(section: string): void {
    switch (section) {
      case 'detailData':
        this.isPropertyDataShown = !this.isPropertyDataShown;
        break;
      case 'equipmentData':
        this.isPropertyEquipmentShown = !this.isPropertyEquipmentShown;
        break;
      case 'descriptionData':
        this.isPropertyDescriptionShown = !this.isPropertyDescriptionShown;
        break;
      case 'energyData':
        this.isPropertyEnergyShown = !this.isPropertyEnergyShown;
        break;
    }
  }

  public goToBroker(): void {
    this.router.navigate(['/broker']);
  }
}
