import {
  Component,
  EventEmitter,
  Input, Output
} from '@angular/core';

import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';

@Component({
  selector: 'app-marker-image-window',
  templateUrl: './marker-image-window.component.html',
  styleUrls: ['./marker-image-window.component.css']
})
export class MarkerImageWindowComponent {
  @Input() marker: ExtendedMarkerModel = null;
  @Input() isBigContentHeight: boolean = false;

  @Output() loadImages: EventEmitter<void> = new EventEmitter<void>();

  public _name: string = 'marker-image-window';

  public goToLoadImages(): void {
    this.loadImages.emit();
  }
}
