import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnChanges
} from '@angular/core';
import { Router } from '@angular/router';

import { DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';

import {
  MarkerDetailContactMeWindowComponent
} from '../../../../shared/components/marker-detail-contact-me-window/marker-detail-contact-me-window.component';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { OpenModalService } from '../../../../core/services/open-modal.service';
import { UserService } from '../../../../core/services/user.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { PriceTrends } from '../../../../core/models/marker-price-trend.model';
import { SignupMobileMenuComponent } from '../../../registration/components/signup-mobile-menu/signup-mobile-menu.component';

@Component({
  selector: 'app-market-view-property',
  templateUrl: './market-view-property.component.html',
  styleUrls: ['./market-view-property.component.css']
})
export class MarketViewPropertyComponent implements OnChanges {
  @Input() marker: any;
  @Input() userNotAuthorized: boolean;

  @Output() makeMeOwner: EventEmitter<string> = new EventEmitter<string>();

  public _name: string = 'market-view-property';

  public isSmallMobileDevice: boolean = isSmallMobileDevice();

  public priceTrends: PriceTrends;
  public priceTrendsLoading: boolean;

  constructor(
    private dialogService: DialogService,
    private modalService: NgxModalStack,
    private openModalService: OpenModalService,
    private userService: UserService,
    private propertiesRepositoryService: PropertiesRepositoryService,
    private router: Router,
  ) {}

  public ngOnChanges(): void {
    this.getPriceTrends();
  }

  public async getPriceTrends(): Promise<void> {
    this.priceTrendsLoading = true;
    this.priceTrends = await this.propertiesRepositoryService.getAllTrends(this.marker.id);
    this.priceTrendsLoading = false;
  }

  public makeMePropertyOwner(): void {
    this.makeMeOwner.emit('own property from market-view');
  }

  public showSignup(): void {
    if (this.isSmallMobileDevice) {
      this.openSignUpMobileMenu();
    } else {
      this.openSignUpModal();
    }
  }

  public async openContactMeForm({
    type,
    title = 'Stelle dem Eigentümer eine Frage!',
    buttonText = 'Frage abschicken',
  }): Promise<any> {
    this.openModalService.open();
    this.dialogService.addDialog(
      MarkerDetailContactMeWindowComponent,
      {type, title, buttonText, propertyId: this.marker.id}
    );
    this.openModalService.close();
  }

  private openSignUpModal(): void {
    this.router.navigate([{ outlets: { 'modal': [ 'signup'] }}]);
  }

  private openSignUpMobileMenu(): void {
    this.modalService.openFromComponent(SignupMobileMenuComponent);
  }

  public get userRole(): string {
    return this.userService.role;
  }
}
