import {
  Component,
  EventEmitter,
  Output, Input, Inject,
  ChangeDetectionStrategy
} from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { isSmallMobileDevice, isTabletDeviceLandscaped } from '../../../../shared/utils/responsive.util';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { FeatureToggleService } from '../../../../core/services/feature-toggle/feature-toggle.service';
import { WINDOW } from '../../../../core/services/window.provider';
import { getEstimatedPriceText, getSalePriceText } from '../../../../shared/utils/price-title.util';
import { ActionWithOfferPrice } from '../../../../shared/components/marker-detail-make-me-move/marker-detail-make-me-move.component';

type PropertyViewMode = 'dynamic' | 'static';
const PROPERTY_DETAIL_ASSETS_URL: string = '../../../assets/images/property_view_imgs/';

export interface PropertyDetailModalAction {
  type: string;
  title: string;
  buttonText: string;
}

@Component({
  selector: 'app-property-detail-header',
  templateUrl: './property-detail-header.component.html',
  styleUrls: ['./property-detail-header.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PropertyDetailHeaderComponent {
  @Input() activeMarker: ExtendedMarkerModel;
  @Input() offerPrice: number = null;

  @Output() makeMeOwner: EventEmitter<string> = new EventEmitter<string>();
  @Output() toggleMakeMeMoveModal: EventEmitter<ActionWithOfferPrice> = new EventEmitter<ActionWithOfferPrice>();
  @Output() toggleContactMeForm: EventEmitter<PropertyDetailModalAction> = new EventEmitter<PropertyDetailModalAction>();
  @Output() loadImages: EventEmitter<null> = new EventEmitter<null>();
  @Output() toggleSlideshow: EventEmitter<null> = new EventEmitter<null>();

  public _name: string = 'property-detail-header';
  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isTabletDeviceLandscaped: boolean = isTabletDeviceLandscaped();
  public propertyView: PropertyViewMode = 'static';
  public tooltipTextFirst: string = 'Der Schätzwert basiert auf mehreren Variablen und wird von Scoperty mit einem ' +
    'speziellen Algorithmus berechnet.';
  public tooltipTextSecond: string = 'Hier kannst du dem Eigentümer ein unverbindliches Angebot für seine Immobilie machen.';

  public get markerTitle(): string {
    return this.activeMarker.Title ? this.activeMarker.Title : this.fullAdress;
  }

  public get isPropertyWithOwner(): boolean {
    return !!this.activeMarker.Owner;
  }

  public get ownerOrRealtorExist(): boolean {
    return !!this.activeMarker.Owner || !!this.activeMarker.Realtor;
  }

  public get hasImages(): boolean {
    return this.activeMarker.images != null && this.activeMarker.images.length > 0;
  }

  public get position(): google.maps.LatLngLiteral {
    const {Latitude, Longitude} = this.activeMarker;
    return {lat: Latitude, lng: Longitude};
  }

  private get fullAdress(): string {
    const {PostalCode, Locality, Street, HouseNumber} = this.activeMarker;
    if (PostalCode && Locality && Street && HouseNumber) return `${Street} ${HouseNumber}, \n ${PostalCode} ${Locality}`;
    else return 'Keine Adresse';
  }

  public get markerAddress(): string {
    return this.activeMarker.Title ? this.fullAdress : '';
  }

  public get activeMarkerStreetViewUrl(): string {
    return this.isTabletDeviceLandscaped
      ? `${PROPERTY_DETAIL_ASSETS_URL}visual_map.svg`
      : `${PROPERTY_DETAIL_ASSETS_URL}hero_image.svg`;
  }

  public get layerAvailable(): boolean {
    if (this.activeMarker.isPropertyOfTypeMFH && this.activeMarker.hasApartments) return false;
    return !this.hasImages;
  }

  public get markerPrice(): number {
    if (this.activeMarker.isStatusForSale && this.activeMarker.SalePrice) {
      return this.activeMarker.SalePrice;
    } else {
      return this.activeMarker.EstimatedPrice;
    }
  }

  public get headerQuestion(): string {
    if (this.activeMarker.isPropertyOfTypeEFH) {
      return 'GEFÄLLT DIR DAS HAUS?';
    } else if (this.activeMarker.isPropertyOfTypeMFH) {
      return 'INTERRESSIERST DU DICH FÜR EINE DER WOHNUNGEN?';
    } else {
      return 'GEFÄLLT DIR DIE IMMOBILIE?';
    }
  }

  public get backgroundImage(): string {
    return this.hasImages
      ? `url( ${this.activeMarker.firstImage} )`
      : `url( ${this.activeMarkerStreetViewUrl} )`;
  }

  public get priceTitle(): string {
    return this.activeMarker.isStatusForSale
      ? getSalePriceText(this.activeMarker.PropertyType).toUpperCase()
      : getEstimatedPriceText(this.activeMarker.PropertyType).toUpperCase();
  }

  public get estimatedPriceTitle(): string {
    return getEstimatedPriceText(this.activeMarker.PropertyType);
  }

  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: Window,
    public featureToggleService: FeatureToggleService,
  ) {
  }

  public goToMakeMeOwner(event): void {
    this.makeMeOwner.emit(event);
  }

  public openMakeMeMoveModal(event): void {
    this.toggleMakeMeMoveModal.emit(event);
  }

  public openContactMeForm(event): void {
    this.toggleContactMeForm.emit(event);
  }

  public openSlideshow(): void {
    this.toggleSlideshow.emit();
  }

  public goToLoadImages(): void {
    this.loadImages.emit();
  }

  public switchPropertyView(mode: PropertyViewMode): void {
    this.propertyView = mode;
  }
}
